  top: 0.4rem;
  right: 0.4rem;
  bottom: 0.4rem;
  left: 0.4rem;
  z-index: -1;
  transition: all 0.2s linear;
  border-radius: inherit; /* !importanté */
  border: 1px solid transparent;
  background-color: #2194FF;
  background-image: url("../../img/btn1.svg"), url("../../img/btn2.png");
