

.volume{
  background-color: rgba(33,148,255,.05);

  padding-top: 3.4rem;
  padding-bottom: 6.4rem;
  text-align: center;
  @include br($lg){
    padding-bottom: 4.4rem;
  }
  @include br($lt){
    padding-bottom: 3.4rem;
  }
  img{

    
    display: block;
    margin: 4rem auto;
    @include br($lg){
      max-width: 60%;
      font-size: 3rem;
    }
}

  &__title{
    font-family: $fontRus;
  color: $blue;
  @include fontSize(36,43);
    text-align: center;
    margin-bottom: 3.5rem;

    @include br($lg){
      font-size: 3rem;
    }
    @include br($lt){
      font-size: 2.7rem;
    }

    @include br($tb){
      font-size: 2.4rem;
    }
    @include br($mb){
      font-size: 2rem;
    }
    @include br($sm){
      font-size: 1.5rem;
    }
  }

  &__buttons{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    
    .volume__btn{
      width: 20%;
      padding: 0.9rem 3rem;
      margin: 0 1.2rem;
      border-radius: 2rem;
      @include br($sm){
        text-align: center;
      }
      @include br($lg){
        font-size: 1.6rem;
        padding: 0.7rem 2.5rem;
        min-width: 14.5rem;
      }
    
      @include br($lt){
        
        font-size: 1.4rem;
        padding: 0.5rem 3rem;
      }

      @include br($tb){
        margin: 0 .8rem;
        padding: 0.5rem 1.5rem;
        font-size: 1.2rem;
        min-width: 11.5rem;
      }
      @include br($mb){
        
        width: 45%;
        margin-bottom: 2rem;
        padding: 1rem 1.5rem;
      }
      @include br($sm){
        
        font-size: .8rem;
        width: 100%;
      }
    }
    .pn{
      padding: 0.9rem 1.5rem;
    }
  }
}

.volume__btn_2{
  width: 30.5rem;
  padding: 1em 3em;
  margin-top: -3rem;
  @include br($lg){
    
  width: 25.5rem;
  }
}