

.office{
  padding-bottom: 6.5rem;
text-align: center;
  &__title{
    font-family: $fontRus;
    color: $blue;
    @include fontSize(36,43);
    text-align: center;
    margin-bottom: 2.7rem;
    width: 69rem;
    margin: 0 auto 1.1rem;

    @include br($lg){
      font-size: 3rem;
     width: 50rem;;
    }
    @include br($lt){
      width: 100%;
      font-size: 2.7rem;
    }
  
    @include br($tb){
      font-size: 2.4rem;
    }
    @include br($mb){
      font-size: 2rem;
    }
    @include br($sm){
      font-size: 1.5rem;
    }
  }

  &__button{
    padding: 2.9rem 5rem;
    width: 29.5rem;
    margin-bottom: 4rem;

    @include br($lg){
      padding: 2.6rem 4rem;
      width: 24.5rem;
      margin-bottom: 3rem;
     }
     @include br($lt){
      
      padding: 2.4rem 3.6rem;
      width: 20.5rem;
      margin-bottom: 2rem;
     }

     @include br($tb){
      padding: 2.2rem 3rem;
      width: 19rem;
      margin-bottom: 2rem;
      }
    
      @include br($mb){
        padding: 1.5rem 1rem;
      width: 15rem;
      }
      @include br($sm){
        
      }
  }
  &__content{
    display: flex;
    margin-bottom: 4rem;
    background: $white;
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.25);
    border-radius: 33px;
  }
}

.office__left{
  width: 50%;
  text-align: left;
  padding: 4rem;


  @include br($lg){
    padding: 3rem;
    text-align: center;
   }
   @include br($lt){
    padding: 2rem;
   }
  &_title{
    letter-spacing: 0.05em;
    text-transform: uppercase;
    font-family: $fontRus;
    color: $blue;
    @include fontSize(36,43);
    margin-bottom: 1.6rem;

    @include br($lg){
      font-size: 3rem;
    }
    @include br($lt){
      font-size: 2.7rem;
    }
  
    @include br($tb){
      font-size: 2.4rem;
    }
    @include br($mb){
      font-size: 2rem;
    }
    @include br($sm){
      font-size: 1.5rem;
    }
  }

  &_subtitle{
    letter-spacing: 0.05em;
    text-transform: uppercase;
    color: #1B2639;
    @include fontSize(18,25);
    padding-bottom: 2.1rem;
    border-bottom: 1px solid rgba(27, 38, 57, 0.2);
    margin-bottom: 3.1rem;

    @include br($lg){
      font-size: 1.6rem;
      margin-bottom: 1.8rem;
      text-align: center;
     }
     @include br($lt){
      font-size: 1.4rem;
      margin-bottom: 1.5rem;
     }

     @include br($tb){
      font-size: 1.3rem;
      margin-bottom: 1rem;
    }
    @include br($mb){
      font-size: 1rem;
    }
    @include br($sm){
      font-size: .8rem;
    }
  }

  &_text{
    color:$black;
    @include fontSize(18,25);
    margin-bottom: 4.5rem;
    span{
      color:$blue;
      text-transform: uppercase;
    }

    @include br($lg){
      font-size: 1.6rem;
      margin-bottom: 3rem;
     }
     @include br($lt){
      font-size: 1.4rem;
      margin-bottom: 2rem;
     }

     @include br($tb){
      font-size: 1.2rem;
      margin-bottom: 1rem;
    }
    @include br($mb){
      font-size: 1rem;
    }
    @include br($sm){
      font-size: .8rem;
    }
  }
}

.office__right{
  width: 50%;
  img{
    border-radius: 0px 33px 33px 0px;
    width: 100%;
    height: 100%;
    object-fit: cover;	
  }
}

.links{
  display: flex;
  justify-content: space-between;

  .link__item{
    text-decoration: underline;
    color:$white;
    @include fontSize(14,19);
    &:hover{
      color:darken($white,50);
    }

    @include br($lg){
      font-size: 1.2rem;
    }

    @include br($tb){
      font-size: 1rem;
    }
    @include br($sm){
      font-size: .8rem;
    }
  }
}