

.intro{
  background-color: $black;
  background-image: url("../../img/introBG.png");
  background-position: bottom 5.8rem right 0;
  background-repeat: no-repeat;
  overflow: hidden;
@include br(1860px){
  background-position: bottom 5.8rem right -15rem;
}

@include br(1715px){
  background-position: bottom 5.8rem right -25rem;
}
@include br(1430px){
  background-size: 70%;
  background-position: bottom 30% right -20rem;
}

@include br($mb){
  
  background-position: bottom 30% right -15rem;
}
@include br($sm){
  font-size: 1rem;
}


  &__title{
    font-size: 6.4rem;
    line-height: 1;
    font-family: $fontRus;
    font-weight: 400;
    color: $blue;
    width: 44.7rem;
    @include br($lt){
      font-size: 5rem;
    }

    @include br($tb){
      font-size: 3rem;
      max-width: 20rem;
    }
    @include br($mb){
      font-size: 2rem;
    }
    @include br($sm){
      font-size: 1rem;
    }
  }

  &__subtitle{
    position: relative;
    margin-top: 1.4rem;
    margin-bottom: 4rem;
    font-family: $fontRus;
    @include fontSize(24,29);
    color:$white;
    width: 30rem;
    &::before{
      content: "";
      position: absolute;
      bottom: -3rem;
      right: -10rem;
      z-index: 10;
      height: 100px;
      width: 100px;
      
      background: url("../../img/BigArrow.svg");
      @include br($lg){
        bottom: -4rem;
        transform: rotate(20deg);
      }
      @include br($lt){
        right: -6rem;
        bottom: -5rem;
      }
      
      @include br($tb){
        right: -10rem;
        bottom: -3.5rem;
       
      }
      @include br($mb){
        display: none;
      }
      
    }
    
    @include br($lt){
      font-size: 2rem;
    }

    @include br($tb){
      font-size: 1.5rem;
      max-width: 20rem;
    }
    @include br($mb){
      font-size: 1rem;
      margin-bottom: 2rem;
    }
    @include br($sm){
      font-size: .7rem;
    }
  }
}
.intro__left{
  width: 60%;
  padding-bottom: 7.2rem;
  position: relative;
  
  @include br($lt){
    padding-bottom: 5rem;
  }
  @include br($lg){
    padding-bottom: 5rem;
  }

  @include br($tb){
    padding-bottom: 4rem;
  }
  @include br($mb){
    padding-bottom: 3rem;
  }
  @include br($sm){
    padding-bottom: 2rem;
  }
  

  .intro__text{
    font-weight: 600;
    color: rgba($white,0.8);
    margin-left: 2rem;
    margin-top: 1.3rem;
    @include fontSize(16,22);

    @include br($lg){
      font-size: 1.4rem;
    }
    @include br($lt){
      font-size: 1.2rem;
    }

    @include br($tb){
      font-size: 1rem;
    }
    @include br($mb){
      font-size: .8rem;
      margin-left: 0;
    }
    @include br($sm){
      font-size: .6rem;
    }
  }
}

.intro__inner{
  display: flex;
}

.intro__logo{
  display: flex;
  img{
    margin: 1.8rem 2.4rem 3.3rem 0;
    @include br($lt){
      width: 24rem;
    }

    
    @include br($tb){
      width: 20rem;
      margin: 1.6rem 2.2rem 3rem 0;
    }
    @include br($mb){
      width: 17rem;
      margin: 1.4rem 1rem 2.6rem 0;
    }
    @include br($sm){
      width: 14rem;
      margin: 1.2rem 1.8rem 2rem 0;
    }
  }
}

.intro__logo_text{
  color:$blue;
  width: 15rem;
  height: 3.6rem;
  font-style: italic;
  font-size: 1.6rem;
  line-height: 1.125em;
border-left: 1px solid #FFF;
padding-left: 2.1rem;
margin-top: 60px;
@include br($lt){
  font-size: 1.4rem;
  margin-top: 5.8rem;
}

@include br($tb){
  font-size: 1.2rem;
  margin-top: 5.6rem;
}
@include br($mb){
  font-size: 1rem;
  padding-left: .5rem;
  height: 2.6rem;
}
@include br($sm){
  font-size: .7rem;
  margin-top: 5.4rem;
}
}

.intro__weather{
  display: flex;
  margin-bottom: 2rem;

  @include br($tb){
    margin-bottom: 1.5rem;
  }
  @include br($mb){
    margin-bottom: 1.3rem;
  }
  @include br($sm){
    margin-bottom: 1rem;
  }
  &_item{
    margin-right: 3.4rem;

    @include br($tb){
      margin-right: 2rem;
    }
    @include br($mb){
      margin-right: 1.7rem;
    }
    @include br($sm){
      margin-right: 1rem;
    }
  }

  &_icons{
    display: flex;
    img{
      height: 2rem;
      width: 2rem;
      margin: 0 0.4rem;

      @include br($tb){
        margin-right: 1rem;
        height: 1.5rem;
      width: 1.5rem;
      }
      @include br($mb){
        margin-right: 0.7rem;
        height: 1.2rem;
      width: 1.2rem;
      }
      @include br($sm){
        margin-right: .5rem;
        height: 1rem;
      width: 1rem;
      }
    }
    margin-bottom: 0.7rem;
  }
  &_text{
    color: rgba($white,0.5);
    font-size:  1.6rem;
    line-height: 1.375rem;
    font-weight: 300; 
    @include br($tb){
      font-size: 1rem;
    }
    
  }

  &:last-child{
    margin-right: 0;
  }
}

.intro__right{
  width: 40%;
  padding-left: 21.7rem;
  @include br($lg){
    padding-left: 15.7rem;
    padding-top: 3rem;
  }

  @include br($tb){
    padding-left: 8rem;
  }
  @include br($mb){
    padding-left: 7rem;
  }
  @include br($sm){
    padding-left: 5rem;
  }

  
  
  

  .intro__number{
    margin-top: 2.4rem;
    margin-bottom: 0.5rem;
    font-family: $fontRus;
    font-weight: 400;
    @include fontSize(24,29);
    color:$white;
    @include br($lg){
      font-size: 2rem;
    }
    @include br($lt){
      font-size: 1.7rem;
    }

    @include br($tb){
      font-size: 1.2rem;
    }
    @include br($mb){
      font-size: .8rem;
    }
    @include br($sm){
      font-size: .7rem;
    }
  }

  .intro__question{
    text-decoration: underline;
    @include fontSize(16,22);
    font-weight: 700;
    color:$blue;
    @include br($lt){
      font-size: 1.3rem;
    }
    @include br($tb){
      font-size: 1.1rem;
    }
    @include br($mb){
      font-size: .6rem;
    }
    @include br($sm){
      font-size: .4rem;
    }
    
    &:hover{
      color: darken($blue, 20);
    }

  }
}
