

.question{
  background-color: rgba(33,148,255,.05);
  padding-top: 2.4rem;
  padding-bottom: 6.4rem;

  @include br($lg){
    padding-bottom: 5.4rem;
  }
  @include br($lt){
    padding-bottom: 4.4rem;
  }

  &__title{
    font-family: $fontRus;
  color: $blue;
  @include fontSize(36,43);
    text-align: center;
    margin-bottom: 2.7rem;

    @include br($lg){
      font-size: 3rem;
      margin-bottom: 2rem;
    }
    @include br($lt){
      font-size: 2.7rem;
    }

    @include br($tb){
      font-size: 2.4rem;
    }
    @include br($mb){
      font-size: 2rem;
    }
    @include br($sm){
      font-size: 1.5rem;
    }
  }
}

.accordion{

  .accordion__item{
    padding: 2.4rem 0;
    border-top: 1px solid rgba(white,.1);
    border-bottom: 1px solid rgba(white,.1);
    @include br($lg){
      padding: 2rem 0;
    }
    @include br($lt){
      padding: 1.8rem 0;
    }

    
  }

  &__header{
    position: relative;
    text-transform: uppercase;
    font-weight: 700;
    color:$white;
    margin-bottom: 1.6rem;
    padding-left: 7rem;
    @include fontSize(16,22);
    @include br($lg){
      font-size: 1.4rem;
      margin-bottom: 1.3rem;
      padding-left: 5rem;
    }
    @include br($lt){
      font-size: 1.2rem;
      margin-bottom: 1rem;
      padding-left: 4rem;
    }

    @include br($tb){
      font-size: 1.2rem;
    }
    @include br($mb){
      font-size: 1rem;
    }
    @include br($sm){
      font-size: .8rem;
    }

    &::before {
      position: absolute;
      left: 0;
      top: 0;
      z-index: 10;
      margin: 0 2.3rem;
      width: auto;
      background-image: url("../../img/accordion/up.png");
   
      background-repeat: no-repeat;
      background-size: 20px;
      content: "ar";
      color:transparent;

      @include br($lg){
        background-size: 17px;
      }
      @include br($lt){
        background-size: 14px;
      }
    }
    

  }

  &__body{
    font-weight: 300;
    color:$white;
    padding-left: 7rem;
    @include fontSize(16,22);

    @include br($lg){
      font-size: 1.4rem;
    }
    @include br($lt){
      font-size: 1.2rem;
      padding-left: 5rem;
    }

    @include br($tb){
      font-size: 1.2rem;
      padding-left: 4rem;
    }
    @include br($mb){
      font-size: 1rem;
      padding-left: 3rem;
    }
    @include br($sm){
      font-size: .8rem;
    }
  }

}
.accordion__item_show .accordion__header{
  color:$blue;
  
  &::before {
  background-image: url("../../img/accordion/down.png");
  }
}
.accordion__item:not(.accordion__item_show) .accordion__body {
  display: none;
}