

.price{
  padding-top: 4.6rem;
  background-color: rgba($blue,0.05);
  position: relative;

  @include br($tb){
    padding-top: 3rem;
  }
  @include br($mb){
    padding-top: 2rem;
    
  }

  &__arrow_1{
    position: absolute;
    left: 50%;
    bottom: 0;
    z-index: 10;
    transform: translate(-50%,0);
    @include br($tb){
      display: none;
    }
  }
  &__arrow_2{
    position: absolute;
    left: 50%;
    bottom: 0;
    z-index: 10;
    transform: translate(-50%,50%);
    @include br($tb){
      display: none;
    }
  }
  &__arrow_3{
    position: absolute;
    left: 50%;
    bottom: 0;
    z-index: 10;
    transform: translate(-50%,75%);
    @include br($tb){
      display: none;
    }
  }
 
}

.price__title{
  font-family: $fontRus;
  color: $blue;
  @include fontSize(36,43);
    text-align: center;
   
    @include br($lg){
      font-size: 3rem;
    }
    @include br($lt){
      font-size: 2.7rem;
    }

    @include br($tb){
      font-size: 2.4rem;
    }
    @include br($mb){
      font-size: 2rem;
    }
    @include br($sm){
      font-size: 1.5rem;
    }
}

.price__content{
  display: flex;
  flex-wrap: wrap;
}

.price__propan{
  width: 32%;
  padding-top: 7.6rem;
  padding-left: 3rem;
  @include br($lg){
    font-size: 3rem;
  }

  @include br($tb){
    font-size: 2.1rem;
    padding-top: 3.6rem;
    width: 35%;
    padding-left: 1rem;
  }

  
  @include br($mb){
    order:1;
    font-size: 1.7rem;
    padding-top: 1rem;
    text-align: center;
    width: 100%;
  }
  @include br($sm){
    font-size: 1.3rem;
  }
  .price__propan_title{
    font-family: $fontRus;
    @include fontSize(36,43);
    color:$white;
    margin-bottom: 1rem;
    @include br($lg){
      font-size: 3rem;
    }
    @include br($lt){
      font-size: 2.5rem;
    }

    @include br($tb){
      font-size: 2.1rem;
    }
    @include br($mb){
      font-size: 1.7rem;
    }
    @include br($sm){
      font-size: 1.3rem;
    }
  }

  .price__propan_subtitle{
    @include fontSize(24,29);
    color: $blue;
   
    font-family: $fontRus;
    @include br($lg){
      font-size: 2.1rem;
    }
    @include br($lt){
      font-size: 1.8rem;
    }

    @include br($tb){
      font-size: 1.5rem;
    }
    @include br($mb){
      font-size: 1.2rem;
    }
    @include br($sm){
      font-size: 1rem;
    }
  }

  .price__propan_text{
    font-weight: 300;
    min-height: 11rem;
    @include fontSize(16,22);
    color: rgba($white,.7);
    margin: 2.8rem 0;
    padding-right: 1.7rem;
    font-family: $fontOp;
    @include br($lg){
      margin: 1.8rem 0;
    }
    @include br($lt){
      margin: 1rem 0;
    }

    @include br($tb){
      font-size: 1.4rem;
    }

    @include br(660px){
      min-height: 14rem;
    }
    @include br($mb){
      min-height: 1px;
      font-size: 1.2rem;
    }
    @include br($sm){
      font-size: 1rem;
    }
  }
  .price__propan_btn{
    margin-bottom: 9.6rem;
    @include br($lg){
      margin-bottom: 6.6rem;
    }
    @include br($lt){
      margin-bottom: 3.6rem;
    }

    @include br($mb){
      margin-bottom: 1rem;
  }
}
}


.price__percent{
    width: 32%;
    text-align: center;
    margin-top: 4.9rem;
    padding-bottom: 4.4rem;
    justify-content: center;

    @include br($lg){
      width: 34%;
    }
    @include br($lt){
    }
    @include br($tb){
      width: 30%;
    }
    @include br($mb){
      order:3;
      width: 100%;
    }
    @include br($sm){
    }

  &_title{
    font-weight: 400;
    @include fontSize(20,24);
    color:rgba($white,.7);
    text-align: center;
    font-family: $fontRus;
    margin-bottom: 1.3rem;
    @include br($lg){
      font-size: 1.8rem;
    }
    @include br($lt){
      font-size: 1.5rem;
    }

    @include br($tb){
      font-size: 1.2rem;
    }
    @include br($mb){
      font-size: 1rem;
    }
    @include br($sm){
      font-size: .7rem;
    }

    
  }

  &_subtitle{
    justify-content: center;
    align-items: center;
    display: flex;
    margin-bottom: 1.5rem;

    

    .percent__item{
      width: 8rem;
      margin: 1.5rem 2.5rem;
      font-weight: 400;
      font-family: $fontRus;
      color:rgba($white,.7);
      @include fontSize(36,18);

      @include br($lg){
        font-size: 3.4rem;
      }
      @include br($lt){
        font-size: 3rem;
      }
  
      @include br($tb){
        font-size: 2.5rem;
      }
      @include br($mb){
        font-size: 2rem;
      }
      @include br($sm){
        font-size: 1.7rem;
      }
      span{
        @include fontSize(18,18);
        margin: 0;

        @include br($lg){
          font-size: 1.6rem;
        }
        @include br($lt){
          font-size: 1.4rem;
        }
    
        @include br($tb){
          font-size: 1.2rem;
        }
        @include br($mb){
          font-size: 1rem;
        }
        @include br($sm){
          font-size: .8rem;
        }
      }
      &:last-child{
        border-left: 1px solid rgba($white, 0.3);
        padding-left: 2.5rem;
        margin-left: 0;
      }
    }

   
  }

  img{
    margin: 0 auto;
    @include br($tb){
      max-width: 80%;
    }
  }

}