.btn{
  position: relative;
  z-index: 100;
  color: $white;
  font-family: $fontRus;
  font-weight: 400;
  @include fontSize(22,27);
  text-shadow: 1px 2px 0px rgba(0, 0, 0, 0.25);
  text-align: center;
  width: 39.5rem;
  padding: 0.68em 2.59em;
  
 cursor: pointer;
  border-radius: 1.1rem;

  @include br($lg){
    font-size: 1.8rem;
    width: 34.5rem;
  }

  @include br($lt){
    font-size: 1.7rem;
    width: 30.5rem;
  }

  @include br($tb){
    font-size: 1.3rem;
    max-width: 25rem;
  }
  @include br($mb){
    font-size: 1rem;
    max-width: 18rem;
  }
  @include br($sm){
    font-size: .7rem;
    max-width: 10rem;
  }

&:before {
  content: '';
  position: absolute;
  top: 0; right: 0; bottom: 0; left: 0;
   z-index: -10;
 // margin: -$border; /* !importanté */
  border-radius: inherit; /* !importanté */
  background: linear-gradient(to right,#2194FF , #81C0FB,#108CFF);
  
}
&:after{
  content: '';
  position: absolute;
  top: .4rem; 
  right: .4rem;
   bottom: .4rem;
   left: .4rem;
   z-index: -1;
   transition:all .2s linear;
 // margin: -$border; /* !importanté */
  border-radius: inherit; /* !importanté */
  border: 1px solid transparent;
  background-color: $blue;
  background-image: url("../../img/btn1.svg"),url("../../img/btn2.png");
  background-repeat: no-repeat,no-repeat;
  background-position: top .4rem left .4rem, bottom .4rem right .4rem;
  box-shadow: inset 0 0.4rem 1.2rem rgba(0, 0, 0, 0.45);
}
  &:hover{
    box-shadow:  0 0 1rem rgba($white,.7);
  }

}

.btn_round{
  font-weight: 700;
  color:$white;
  background-color: $blue;
  @include fontSize(18,25);
  text-align: center;
  padding: 0.9rem 3.4rem;
  border-radius: 5rem;
  transition:all .2s linear;

  @include br($lg){
    font-size: 1.6rem;
    
  }

  @include br($lt){
    font-size: 1.4rem;
  }


  @include br($tb){
    font-size: 1.2rem;
  }
  @include br($mb){
    font-size: 1rem;
  }
  @include br($sm){
    font-size: .8rem;
  }

  &:hover{
    background-color:rgba($blue,0.7);
    transform: translateY(-.3rem);
  }
}


