
.benefits{
  padding-top: 5.7rem;

  @include br($lg){
    padding-top: 4.7rem;
  }

  @include br($lt){
    padding-top: 3.7rem;
  }

  @include br($tb){
    padding-top: 2.7rem;
  }
  @include br($mb){
    padding-top: 1.7rem;
  
  }

  &__title{
    color:$white;
  font-family: $fontRus;
  @include fontSize(36,43);
  text-align: center;
  margin-bottom: 2.5rem;
  @include br($lg){
    font-size: 3rem;
  }
  @include br($lt){
    font-size: 2.7rem;
  }

  @include br($tb){
    font-size: 2.4rem;
  }
  @include br($mb){
    font-size: 2rem;
  }
  @include br($sm){
    font-size: 1.5rem;
  }

  }

  &__content{
    display: flex;
    flex-wrap: wrap;
  }

  &__item{
    width: 33.3333%;
    padding: 0 5.8rem;
    text-align: center;
    margin-bottom: 3rem;
    @include br($lg){
      padding: 0 3rem;
    }
  
    @include br($mb){
      width: 50%;
    }
    @include br($sm){
    }
    &_img{
      margin-bottom: 0.8rem;
    }

    &__title{
      @include fontSize(24,29);
      color: $blue;
      padding: 0 4rem;
      font-family: $fontRus;
      @include br($lg){
        min-height:  5rem;
        font-size: 2rem;
        padding: 0;
      }
    
      @include br($lt){
        font-size: 1.8rem;
        min-height:  4rem;
      }

      @include br($tb){
        font-size: 1.6rem;
        min-height:  4rem;
      }

      @include br($mb){
        min-height:  2rem;
      }
      
    }

    &__text{
      font-weight: 300;
    @include fontSize(16,22);
    color: rgba($white,.7);
    margin: 1.4rem 0;
    padding-right: 1.7rem;
    font-family: $fontOp;
    text-align: center;
    padding: 0 1rem;
    @include br($tb){
      margin: .5rem 0;
      font-size: 1.4rem;
    }
    @include br($mb){
      font-size: 1.2rem;
    }
    @include br($sm){
      font-size: 1rem;
    }
    }

    &_img{
      height: 8rem;
      width: auto;
      @include br($lg){
        height: 6rem;
      }
      @include br($tb){
        height: 5rem;
      }
      @include br($mb){
        height: 4rem;
      }
      @include br($sm){
        height: 3rem;
      }
    }
  }

  
}


.p0{
  padding: 0;
}

.benefits__garant{
  display: flex;
  &_img{
    width: 50%;

    @include br($mb){
      display: none;
    }
  }

  &_content{
    @include br($mb){
    width: 100%;
    margin-bottom: 2rem;
    text-align: center;
  }
  }

  &_title{
    @include fontSize(35,42);
      color: $white;
      padding: 0 ;
      font-family: $fontRus;
      margin-bottom: 2rem;
      @include br($lg){
        font-size: 3rem;
      }
      @include br($lt){
        font-size: 2.7rem;
      }
  
      @include br($tb){
        font-size: 2.4rem;
      }
      @include br($mb){
        font-size: 2rem;
      }
      @include br($sm){
        font-size: 1.5rem;
      }
      span{
        color: $blue;
      }
  }

  &_btn{
    border-radius: 0;
    @include fontSize(14,19);
    padding: 0.7rem;

    @include br($lg){
      font-size: 1.23rem;
      padding: 0.7rem 2rem;
      max-width: 35rem;
    }
    @include br($lt){
      font-size: 1rem;
      max-width: 25rem;
    }
  }

}
