

.ask{
  padding-top: 7.1rem;
  padding-bottom: 3.4rem;

  @include br($lg){

    padding-top: 6rem;
    padding-bottom: 3rem;
  }
  @include br($lt){
    padding-top: 5rem;
    padding-bottom: 2.8rem;
  }

  

  
}

.ask__inner{
  display: flex;
  margin-bottom: 6rem;

  @include br($tb){
    
  margin-bottom: 3rem;
  }

  @include br($mb){
    
  margin-bottom: 2rem;
  }
  @include br($sm){
    margin-bottom: 1rem;
  }
}

.ask__left{
  width: 50%;
  @include br($tb){
    width: 100%
  }
}

.ask__title{
  font-family: $fontRus;
  color: $white;
  @include fontSize(36,43);
    margin-bottom: 5rem;

    @include br($lg){
      font-size: 3rem;
      margin-bottom: 4rem;
    }
    @include br($lt){
      font-size: 2.7rem;
      margin-bottom: 2rem;
    }

    @include br($tb){
      text-align: center;
      font-size: 2.4rem;
      max-width: 30rem;
      margin: 0 auto 1.4rem;
    }

    @include br(700px){
      font-size: 2rem;
    }
    @include br($sm){
      font-size: 1.5rem;
    }
}

.ask__form{
  @include fontSize(12,16);
  text-transform: uppercase;

  color: rgba(45, 45, 45, 0.5);
  @include br($tb){
    text-align: center;
  }
  input,textarea{
    display: block;
    background-color: #fff;
    border: 1px solid #DEDEDE;
    box-shadow: inset 0px 0px 4px rgba(0, 0, 0, 0.25);
    border-radius: 10px;
    padding: 1.8rem 2.2rem;
    margin-bottom: 2.4rem;
    width: 40.2rem;
    resize:none;
    @include br($lg){
      
    padding: 1.6rem 2rem;
    margin-bottom: 2rem;
    width: 35.2rem;
    }
    @include br($lt){
      
    padding: 1.4rem 1.8rem;
    margin-bottom: 1.8rem;
    width: 37rem;
    }

    @include br($tb){
      padding: 1.2rem 1.4rem;
    margin: 0 auto 1.4rem;
    width: 30rem;
    }
    @include br($mb){
      padding: 1rem 1.2rem;
    margin-bottom: 1.2rem;
    width: 25rem;
    }
    @include br($sm){
    margin-bottom: 1rem;
      width: 20rem;
    }
  }
}

.ask__right{
  width: 50%;
  text-align: center;
  position: relative;
  @include br($tb){
    display: none;
  }
  .ask__manager{
    position: relative;
   
      .ask__photo{
        filter: drop-shadow(0px 0px 20px rgba(0, 0, 0, 0.25));
        border-radius: 40px;
       
        @include br($lg){
           max-width: 90%;
          }

         
          
       
      }

      .ask__name{
        background: $blue;
        opacity: 0.8;
        border-radius: 20px;
        position: absolute;
        left: 2rem;
         bottom: 4rem;
        z-index: 10;
        padding: 1.6rem 2.7rem;
        color:$white;
        @include fontSize(16,22);
        text-align: left;
        span{
          font-family: $fontRus;
          @include fontSize(24,29);

          @include br($lg){
            font-size: 2.2rem;
           }
           @include br($lt){
            font-size: 2rem;
           }

           @include br($tb){
            font-size: 1.8rem;
          }
          @include br($mb){
            font-size: 1.6rem;
          }
          @include br($sm){
            font-size: 1.4rem;
          }

        }

        @include br($lg){
          font-size: 1.4rem;
          
        padding: 1.4rem 2.2rem;
         }
         @include br($lt){
          font-size: 1.2rem;
          
        padding: 1.2rem 2rem;
         }

         @include br($tb){
          font-size: 1rem;
          padding: 1rem 1.6rem;
        }
        @include br($mb){
          font-size: 1rem;
          padding: .6rem 1rem;
        }
        @include br($sm){
          font-size: .8rem;
          padding: .4rem .8rem;

        }

      }


  }
}

.company{
  align-items: center;
  display: flex;
justify-content: center;
  flex-wrap: wrap;
  &__item{
    display: block;
    margin: 0 .8rem;
    width: 13.6666%;
    height: 6.6rem;
    position: relative;
    background: $white;
    border-radius: 20px;

    @include br($tb){
      width: 30%;
      margin-bottom: 1rem;
      }
    
      @include br($mb){
        
      width: 48%;
      }
      @include br($sm){
        
      width: 100%;
      }
    &:hover{
      background: darken($white,40);
    }
    img{
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
  }
}