

.modal{
  display: none;
  width: 100%;
  height: 100%;

  position: fixed;
  left: 0;
  top: 0;
  z-index: 1000;
  background-color: rgba($black, .7);

  &--solid{
    
  background-color: #222;
  }

  &.show{
    display: flex;
  }
}

.modal__content{
  
  width: 100%;
  max-width: 46rem;
  margin: auto;
  transition: all .5s linear;

  transform: rotateX(90deg);
}

.modal__delivery{
  position: relative;
  padding: 3rem;
  background-color: #fff;
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.25);
  border-radius: 33px;
}
.modal__close{
  position: absolute;
  right: 3rem;
  top: 3rem;
  z-index: 10;
  transition: all .2s linear;
    &:hover{
        transform: rotate(90deg);
    }
}
.modal__title{
  font-family: $fontRus;
  @include fontSize(24,29);
  letter-spacing: 0.05em;
  text-transform: uppercase;
  max-width: 21rem;
  color: $blue;
}

.modal__form{
  @include fontSize(12,16);
  text-transform: uppercase;
  input{
    display: block;
    background-color: #fff;
    border: 1px solid #DEDEDE;
    box-shadow: inset 0px 0px 4px rgba(0, 0, 0, 0.25);
    border-radius: 10px;
    padding: 1.8rem 2.2rem;
    margin-bottom: 2.4rem;
    width: 40.2rem;
    resize:none;
    
    color: rgba(45, 45, 45, 0.5);

  }


}

.modal__button{
  max-width: 39.5rem;
  padding: 2.9rem 1.7rem;
}

.modal__gratitude{
  width: 100%;
  max-width:110rem;
  margin: 0 auto;
  text-align: center;
  
}

.modal__header{
  display: flex;
  justify-content: space-between;
  margin-bottom: 16rem;
  padding: 0 2rem;
}

.modal__grat_title{
  margin: 0 auto;
  margin-bottom: 3rem;
  width: 100%;
}

.modal__grat_subtitle{
  
  margin: 0 auto;
  margin-bottom: 6rem;
  min-width: 41rem;
  &::before{
    display: none;
  }
}

.modal__grat_btn{
  padding: 2.9rem 1.7rem;
}

