

.reviews{
  padding: 6.6rem 0;
  position: relative;

  @include br($lg){
    padding: 4.6rem 0;
  }
  @include br($lt){
    padding: 3.6rem 0;
  }

  &__title{
    color:$white;
  font-family: $fontRus;
  @include fontSize(36,43);
  text-align: center;
  margin-bottom: 4.7rem;
  @include br($lg){
    font-size: 3rem;
  }
  @include br($lt){
    font-size: 2.7rem;
  }

  @include br($tb){
    font-size: 2.4rem;
  }
  @include br($mb){
    font-size: 2rem;
  }
  @include br($sm){
    font-size: 1.5rem;
  }
  
  }

  &__item{
    position: relative;
    margin: 0 auto;
    display: flex;
    justify-content: center;
    width: 76rem;
    background: $white;
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.25);
    border-radius: 40px;
    padding: 4rem;
    @include br($lg){
      max-width: 65rem;
      padding: 3rem;
    }
    @include br($lt){
      max-width: 60rem;
      padding: 2rem;
    }

    @include br($tb){
      max-width: 45rem;
      text-align: center;
    }
    @include br($mb){
      max-width: 30rem;
      padding: 1rem;
    }
    @include br($sm){
      max-width: 20rem;
    }

    .preview{
      position: absolute;
      left: -8rem;
      bottom: 50%;
      height: 7.2rem;
      width: 4.7rem;
      transform: translateY(50%);

      @include br($tb){
        height: 5.2rem;
        width: 2.7rem;
        left: -4rem;
      }
      @include br($mb){
        left: -3rem;
      }
      
    }
    .next{
      position: absolute;
      right: -8rem;
      bottom: 50%;
      height: 7.2rem;
      width: 4.7rem;
      transform: translateY(50%);

      @include br($tb){
        height: 5.2rem;
        width: 2.7rem;
        right: -4rem;
      }

      @include br($mb){
        right: -3rem;
      }
    }

    .reviews__image{
      width: 35%;
      border-radius: 40px;
      width: 20rem;
      height: 24rem;
      @include br($lg){
        width: 18rem;  
        height: 22rem;
      }
      @include br($lt){
        width: 16rem; 
        height: 20rem;
      }

      @include br($tb){
        width: 14rem; 
        height: 18rem;
      }
      @include br($mb){
        display: none;
      }
      
    }
    &-content{
      width: 65%;
      padding-left: 2.3rem;
      padding-top: 2rem;
      @include br($lg){
        padding-top: 1rem;
      }
      @include br($tb){
        padding-left: 1rem;
      }

      @include br($mb){
        width: 100%;
        padding:0;
      }
    }

    &-name{
      font-weight: 400;
      @include fontSize(24,29);
      color:$blue;
      font-family: $fontRus;;
      margin-bottom: 0.4rem;
      @include br($lg){
        font-size: 2rem;
      }
      @include br($lt){
        font-size: 1.8rem;
      }

      @include br($tb){
        font-size: 1.5rem;
      }
      @include br($mb){
        font-size: 2rem;
      }
      @include br($sm){
        font-size: 1.5rem;
      }
    }

    &-city{
      font-family: 'Russo One';
      font-style: normal;
      font-weight: 400;
      @include fontSize(18,22);

      color: #2D2D2D;
      margin-bottom: 1.5rem;
      @include br($lg){
        font-size: 1.6rem;
        margin-bottom: 1rem;
      }
      @include br($lt){
        font-size: 1.4rem;
      }

      @include br($tb){
        font-size: 1.2rem;
      }
      @include br($mb){
        font-size: 1.5rem;
      }
      @include br($sm){
        font-size: 1rem;
      }
    }

    &-text{
    
      font-weight: 300;
      @include fontSize(16,22);

      color: #2D2D2D;
      @include br($lg){
        font-size: 1.4rem;
        
      }
      @include br($lt){
        font-size: 1.2rem;
        line-height: 1.5em;
      }

      @include br($tb){
        font-size: 1.2rem;
      }
      @include br($mb){
        font-size: 1rem;
      }
      @include br($sm){
        font-size: 1rem;
      }
    }
  }
}

.slick-slider{
  display: flex;
  text-align: center;
}

.slick-track{
  display: flex;
  justify-content: center;
}

.slick-initialized .slick-slide {
  display: flex;
}

.preview,.next{
  position: absolute;
  top: 50%;
  z-index: 100;
  cursor: pointer;
}
.preview{
  left: 14rem;

  @include br($lg){
    left: 12rem;
  }
  @include br($lt){
    left: 8rem;
  }

  @include br($tb){
    left: 6rem;
  }

  @include br(700px){
    left: 2rem;
    height: 5.5rem;
    width: 3.5rem;
  }
  @include br($sm){
    left: 0rem;
  }
}

.next{
  right: 14rem;

  @include br($lg){
    right: 12rem;
  }
  @include br($lt){
    right: 8rem;
  }

  @include br($tb){
    right: 6rem;
  }

  @include br(700px){
    right: 2rem;
    height: 5.5rem;
    width: 3.5rem;
  }
  @include br($sm){
    right: 0rem;
  }
}
